.hero {
  background:url("../img/comp.png") left 78% center no-repeat,
  url("../img/leafter.png") right bottom no-repeat;
  background-color: #61C4D9;
  min-height: 640px;
  color: #fff;
  position: relative;

  &:before {
    content: url("../img/leafter-mini.png");
    position: absolute;
    bottom: -40px;
    left:2%
  }
}
.hero__title-section {
  width: 46%;
  margin-bottom: 64px;
}

.hero__title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.hero__text {
  font-size: 21px;
  margin-bottom: 28px;
  padding-right: 10%;
}

.hero__list {
  display: flex;
  padding: 0;

}

.hero__item {
  display: flex;
  margin-right: 46px;

  &:last-child {
  margin-right: 0;
}}

.hero__description {
  max-width: 85px;
}
